<template>
  <div class="grid md:grid-cols-2 gap-6">
    <div>
      <Input
        v-model="data.firstname"
        label="First Name"
        placeholder="First Name"
        block
        :view="(validation.firstname.$invalid && 'danger') || 'default'"
      />
      <small
        class="text-red-base block"
        v-for="(error, errorIndex) of validation.firstname.$errors"
        :key="errorIndex"
        >{{ error.$message }}.
      </small>
    </div>
    <div>
      <Input
        v-model="data.lastname"
        label="Last Name"
        placeholder="Last Name"
        block
				:view="(validation.lastname.$invalid && 'danger') || 'default'"
      />
      <small
        class="text-red-base block"
        v-for="(error, errorIndex) of validation.lastname.$errors"
        :key="errorIndex"
        >{{ error.$message }}.
      </small>
    </div>
    <div class="md:col-span-2">
      <Input
        v-model="data.email"
        label="Email"
        placeholder="Email address"
        block
        :view="(validation.email.$invalid && 'danger') || 'default'"
      />
      <small
        class="text-red-base block"
        v-for="(error, errorIndex) of validation.email.$errors"
        :key="errorIndex"
        >{{ error.$message }}.
      </small>
    </div>
    <div class="md:col-span-2">
      <Input
        v-model="data.address_text"
        label="Street Address"
        block
        v-gmap="address"
        placeholder="Street address"
        :view="(validation.valid.$invalid && 'danger') || 'default'"
      />
      <small>Street address, P.O box, company name, c/o.</small>
      <small
        class="text-red-base block"
        v-for="(error, errorIndex) of validation.valid.$errors"
        :key="errorIndex"
        >{{ error.$message }}.
      </small>
    </div>
    <div class="md:col-span-2">
      <Input v-model="data.suite" block placeholder="Suite"  />
      <small>Apartment, suite, unit, building, floor, etc.</small>
    </div>
    <div>
      <InputPhone
        ref="refInputPhone"
        v-model="data.phone"
        label="Phone"
        block
        country-autofill-setting
      />
      <small
        class="text-red-base block"
        v-for="(error, errorIndex) of validation.phone.$errors"
        :key="errorIndex"
        >{{ error.$message }}.
      </small>
    </div>
  </div>
</template>

<script>
import { defineComponent, reactive, watch, ref } from 'vue'
import { useVuelidate } from '@vuelidate/core'
import { helpers } from '@vuelidate/validators'
import { required, validAddress, email } from '@/validations/index'
import { phoneNumber } from 'utils/number'
import Input from 'bbUi/components/input/input.vue'
import InputPhone from 'components/InputPhone.vue'

export default defineComponent({
  name: 'AbandonCustomerForm',
  components: {
    Input,
    InputPhone
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        email: null,
        firstname: null,
        lastname: null,
        phone: null,
        address: null,
        suite: null,
        city: null,
        state: null,
        zip: null,
        country: null
      })
    }
  },
  emits: ['update:data'],
  setup(props, { emit }) {
    const refInputPhone = ref(null)

    const data = reactive(props.data)

    Object.assign(data, {
      valid: data.address && data.address !== '' && data.address !== null
    })

    const rules = {
      firstname: {
        required
      },
      lastname: {
        required
      },
      phone: {
        required,
        invalidNumber: helpers.withMessage(
          'Phone number is invalid',
          () => !data.phone ? true : refInputPhone.value?.isValid

        )
      },
      email: {
        required,
        email: helpers.withMessage('Invalid email address', email)
      },
      valid: {
        sameAs: validAddress
      }
    }
    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }
    watch(
      () => ({ ...data }),
      () => {
        emit('update:data', data)
      }
    )
    return {
      phoneNumber,
      data,
      refInputPhone,
      validation,
      validate,
      address: {
				route: 'address',
        valid: 'valid',
        locality: 'city',
        administrative_area_level_1: 'state',
        formatted_address: 'address_text',
        postal_code: 'zip',
        country: 'country',
        disallowCustomAddress: true
      }
    }
  }
})
</script>
