<template>
  <div class="space-y-3 mb-3">
    <p class="font-semibold">{{ title }}</p>
    <div class="flex gap-x-2">
      <i class="material-icons text-base">email</i>
      <div class="overflow-hidden text-ellipsis break-words">
        {{ customer?.email || '-' }}
      </div>
      <i
        class="material-icons ml-2 text-teal-04 hover:text-teal-03 cursor-pointer"
        @click="CopyText(customer?.email)">
        file_copy
      </i>
    </div>
    <div class="flex gap-x-2">
      <i class="material-icons text-base">location_on</i>
      <div class="overflow-hidden text-ellipsis break-words">
        <Address v-if="type === 'online'" :data="addressLocation" />
        <span v-else>{{ addressLocationInStore }}</span>
      </div>
      <i
        v-if="type === 'online'"
        class="material-icons ml-2 text-teal-04 hover:text-teal-03 cursor-pointer"
        @click="CopyText(data?.shipping?.shipping_address_text || data?.customer?.address_text)">
        file_copy
      </i>
      <i
        v-else
        class="material-icons ml-2 text-teal-04 hover:text-teal-03 cursor-pointer"
        @click="CopyText(addressLocationInStore)">
        file_copy
      </i>
    </div>
    <div class="flex gap-x-2">
      <i class="material-icons text-base">call</i>
      <div class="overflow-hidden text-ellipsis break-words">
        {{ phoneNumber(customer?.phone) }}
      </div>
      <i
        class="material-icons ml-2 text-teal-04 hover:text-teal-03 cursor-pointer"
        @click="CopyText(customer?.phone)">
        file_copy
      </i>
    </div>
  </div>
</template>

<script>
import { defineComponent, computed } from 'vue'
import { phoneNumber } from 'utils/number'
import CopyText from 'utils/copy'

import Address from 'components/Address/index.vue'
import Button from 'bbUi/components/button/button.vue'

export default defineComponent({
  name: 'DetailContactOrderInformation',
  components: {
    Address,
    Button
  },
  props: {
    title: {
      type: String,
      default: undefined
    },
    data: {
      type: Object,
      default: () => ({})
    },
    type: {
      type: String, // "local", "online",
      default: 'local'
    }
  },
  setup(props) {
    const customer = computed(() => {
      const { shipping, customer } = props.data

      return ({
        firstName: shipping?.shipping_firstname || customer?.first_name || '-',
        lastName: shipping?.shipping_lastname || customer?.last_name || '-',
        email: shipping?.email || customer?.email || '-',
        phone: shipping?.shipping_phone || customer?.phone || '-',
        addressCustomer:
          customer?.address_text ?
            customer?.suite ? `${customer?.suite}, ${customer?.address_text}`
              : customer?.address_text
            : '-'
      })
    })
    const addressLocation = computed(() => {
      const result = {}
      const keys = ['address1', 'suite', 'city', 'state', 'zip', 'country']
      keys.forEach(n => {
        if (props.data?.shipping) {
          result[n] = props.data?.shipping[`shipping_${n}`] || props.data?.shipping[n]
        }
      })

      return result
    })
    const addressLocationInStore = computed(() => {
      const state = { ...props.data }
      const words = ['address1', 'suite', 'city', ['state', 'zip'], 'country']
      const results = []
      words.forEach(item => {
        if (Array.isArray(item)) {
          const split = []
          item.forEach(child => {
            if (state.customer && state.customer[child]) split.push(state.customer[child])
          })
          if (split.length) results.push(split.join(' '))
        } else {
          if (state.customer && state.customer[item]) results.push(state.customer[item])
        }
      })

      if (!results.length) return undefined
      return results.join(', ')
    })

    return {
      customer,
      addressLocation,
      CopyText,
      phoneNumber,
      addressLocationInStore
    }
  }
})
</script>
<style lang="scss" scoped>
i {
  line-height: 1 !important;
}
</style>