<template>
  <div>
    <p class="font-bold">{{ title }}</p>
    <div class="flex gap-x-4">
      <div class="flex-1 overflow-hidden">
        <a
          :href="data?.order_detail_link || '#'"
          target="_blank"
          class="transition-all block w-full overflow-hidden text-ellipsis whitespace-nowrap truncate text-grey-04 hover:text-teal-02 underline">
          {{ data?.order_detail_link }}
        </a>
      </div>
      <div>
        <Tooltip
          placement="bottom-end"
          :show-after="tooltipDelay"
          :hide-after="100"
          :popper-options="popperOptions">
          <Button
            color="teal"
            dense
            @click="CopyText(data?.order_detail_link || '#')">
            <i class="material-symbols-outlined te">content_copy</i>
          </Button>
          <template #content>
            <p class="max-w-lg">
              {{ data?.order_detail_link || '-' }}
            </p>
          </template>
        </Tooltip>
      </div>
    </div>
  </div>
</template>
<script>
import { defineComponent, ref, onMounted, onUnmounted } from 'vue'
import CopyText from 'utils/copy'

import Button from 'bbUi/components/button/button.vue'

export default defineComponent({
  name: 'DetailCustomerPortalLink',
  components: {
    Button
  },
  props: {
    title: {
      type: String,
      default: undefined
    },
    data: {
      type: Object,
      default: undefined
    }
  },
  setup() {
    const tooltipDelay = ref(200)
    let scrollTimer = null

    const popperOptions = {
      modifiers: [
        {
          name: 'computeStyles',
          options: { adaptive: false }
        }
      ]
    }

    const handleScroll = () => {
      tooltipDelay.value = 400
      clearTimeout(scrollTimer)
      scrollTimer = setTimeout(() => {
        tooltipDelay.value = 200
      }, 150)
    }

    onMounted(() => {
      window.addEventListener('scroll', handleScroll, { passive: true })
    })

    onUnmounted(() => {
      window.removeEventListener('scroll', handleScroll)
      clearTimeout(scrollTimer)
    })

    return {
      CopyText,
      tooltipDelay,
      popperOptions
    }
  }
})
</script>